.main-header{
    background-color: white;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: .5px solid rgba(0, 0, 0, 0.539);

    .left{
        padding: 0 20px;
        display: flex;
        gap: 40px;

        h2{
            color: blueviolet;
            font-size: 30px;
        }
       
    }

    .center{
      
        img{
            height: 20px;
            cursor: pointer;
        }
    }

    .right{
        padding: 0 20px;
        display: flex;
        align-items: center;
        gap: 15px;

        .cart-icon{
            position: relative;
            border: none;
            span{
                width: fit-content;
                text-align: center;
                // border: 1px solid black;
                color: blueviolet;
                padding: 2px 2px;
                position: absolute;
                top: -5px;
                right: -9px;
                line-height: 1;
                border-radius: 10px;
           }
        }

            .account{
                display: flex;
                border: 1px solid black;
                border-radius: 5px 0 0 5px;
                padding: 5px;
                gap: 2px;
                transition: color 1.2s,background-color 1s;
               cursor: pointer;

                &:hover{
                    background-color: rgb(24, 66, 194);
                    color: white;
                    border: 1px solid rgb(24, 66, 194);
                }

                
            li{
                list-style: none;
            }
            }
    }
}